.nateevo-marketPlace-categories{
    padding-bottom: 20px;
    @media only screen and (min-width: 992px){
        padding-bottom: 40px;
    }
    .container{
        width:100%;
        max-width: 1248px;
        padding: 0 15px;
    }
    .marketPlace-categories__header{
        padding: 15px 0 23px;
        @media only screen and (min-width: 992px){
            padding: 25px 0;
        }
        .title{
            font-family: Telefonica;
            font-style: normal;
            font-weight: 300;
            font-size: 32px;
            line-height: 40px;
            text-align: center;
            color: #50535A;
            margin: 0;
            @media only screen and (min-width: 992px){
                font-size: 40px;
                line-height: 50px;
            }
        }
    }
    .marketPlace-categories__shared{
        padding: 10px 0;
        position: sticky;
        top: 0;
        z-index: 2;
        background: #fff;
        @media only screen and (min-width: 992px){
            padding: 16px 0;
            position: relative;
        }
        .form{
            text-align: right;
            &__control{
                width: 100%;
                margin-left: auto;
                position: relative;
                @media only screen and (min-width: 992px){
                    max-width: 328px;
                }
            }
            &__label{
                position: absolute;
                top: 50%;
                transform: translate(0, -50%);
                left: 16px;
                font-family: Telefonica;
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 24px;
                letter-spacing: -0.0032em;
                color: #B6B7B7;
                opacity: 1;
                visibility: visible;
                cursor: text;
            }
            &__input{
                width: 100%;
                height: 48px;
                background: #FFFFFF;
                border: 1px solid #B6B7B7;
                box-sizing: border-box;
                border-radius: 8px;
                font-family: Telefonica;
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 24px;
                letter-spacing: -0.0032em;
                color: #50535A;
                padding: 0 16px;
                &[data-empty="false"],
                &:valid,
                &:focus{
                    outline: none;
                    border-color: #019DF4;
                }
                &[data-empty="false"] + .form__label,
                &:valid + .form__label,
                &:focus +.form__label{
                    opacity: 0;
                    visibility: hidden;
                }
            }
            &__submit{
                position: absolute;
                padding: 0;
                right: 12px;
                width: 20px;
                height: 20px;
                top: 50%;
                transform: translate(0, -50%);
                background: #fff;
                box-shadow: inherit;
                .icon{
                    display: block;
                    color: #86888C;
                    font-size: 20px;
                    &:before{
                        display: none;
                    }
                }                
                &.focus,
                &.active{
                    .icon{
                        color: #019DF4;
                    }
                }
            }
        }
    }
    .marketPlace-categories__back{
        display: none;        
        padding: 30px 0 0;
        @media only screen and (min-width: 992px){
            position: absolute;
            z-index: 2;
            top: 120px;
            padding: 0;
        }
        .btn{
            font-family: Telefonica;
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            text-decoration-line: underline;
            color: #019DF4;
            background: transparent;
            box-shadow: inherit;
            padding: 0;
            @media only screen and (min-width: 992px){
                font-size: 18px;
                line-height: 22px;                
            }
            .icon{
                font-size: 10px;
                transform: rotate(180deg);
                display: inline-block;
                position: relative;
                bottom: 3px;
                margin-right: 10px;
            }
        }
    }
    .card{
        &.horizontal{
            background: #f5f5f5;
            border-radius: 10px;
            box-shadow: inherit;
            margin: 0;
            padding: 10px;
            height: 180px;
            overflow: hidden;
            @media only screen and (min-width: 992px){
                height: 200px;
                padding: 20px 0 20px 20px;
            }
            .card-stacked{
                position: initial;
                z-index: 1;
            }
            .card-image{
                width: 100%;
                min-width: inherit;
                max-width: inherit;
                align-self: flex-end;
                position: absolute;
                right: 0;
                top: 70%;
                text-align: right;
                transform: translate(0, -65%);
                @media only screen and (min-width: 992px){
                    min-width: 180px;
                    max-width: 50%;
                    position: relative;
                    right: inherit;
                    bottom: inherit;
                    top: inherit;
                    transform: inherit;
                }
                img{
                    margin: 0 0 0 auto;
                }
            }
            .card-content{
                padding: 0;
                width: 100%;
                max-width: 165px;
                @media only screen and (min-width: 992px){
                    max-width: 90%;
                }
                .title{
                    font-family: Telefonica;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 24px;
                    line-height: 30px;
                    color: #0B2739;
                    margin: 0;
                    @media only screen and (min-width: 1200px){
                        font-size: 32px;
                        line-height: 40px;
                    }
                }
            }
            .card-action{
                padding: 0;
                border: 0;
                position: initial;
                .btn{
                    font-family: Telefonica;
                    width: auto;
                    min-width: 100px;
                    height: 32px;
                    background: #0B2739;
                    border-radius: 200px;
                    box-shadow: inherit;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 14px;
                    line-height: 32px;
                    letter-spacing: 0.0125em;
                    text-transform: uppercase;
                    color: #FFFFFF;
                    padding: 0 12px 0 16px;
                    &:before{
                        content: '';
                        position: absolute;
                        top: 0;
                        bottom: 0;
                        left: 0;
                        right: 0;
                    }
                    .icon{
                        margin-left: 10px;
                        display: inline-block;
                        vertical-align: baseline;
                        font-size: 12px;
                    }
                }
            }
        }
    }
    .marketPlace-categories__content{
        .list{
            margin: 0 -10px;
            padding: 18px 0 20px;
            @media only screen and (min-width: 992px){
                padding: 25px 0;
                margin: 0 -15px;
            }
        }
        .item{
            padding: 10px;
            @media only screen and (min-width: 992px){
                padding: 15px;
            }
        }
        .item--collapse{
            display: none;
        }
        .see__more{
            width: 100%;
            max-width: 200px;
            margin: 18px auto;
            display: block;
            padding:0;
            font-family: Telefonica;
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 24px;
            text-align: right;
            color: #019BF4;
            background: #fff;
            box-shadow: inherit;
            .icon{
                font-size: 10px;
                margin-left: 8px;
            }
            .text-dinamic{
                &:before {
                    content: 'Cargar más'
                }
            }
            &.active{
                .text-dinamic{
                    &:before {
                        content: 'Ocultar';
                    }
                }
                .icon{
                    display: inline-block;
                    transform: rotate(180deg) translate(0 , 3px)
                }
            }
        }
    }
    .marketPlace-detailCat{
        display: none;
        .marketPlace-detailCat__header{
            .title{
                font-family: Telefonica;
                font-style: normal;
                font-weight: normal;
                font-size: 24px;
                line-height: 30px;
                color: #50535A;
                margin: 0;
                padding: 10px 0;
                @media only screen and (min-width: 992px){
                    padding: 20px 0;
                    font-size: 32px;
                    line-height: 40px;
                }
            }
        }
        .marketPlace-detailCat__content{
            padding: 38px 0 20px;
            @media only screen and (min-width: 992px){
                padding: 40px 0;
            }
            .list{
                margin: -10px;
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                @media only screen and (min-width: 992px){
                    margin: -15px;
                }
            }
            .item{
                padding: 10px;
                margin: 0;
                @media only screen and (min-width: 992px){
                    padding: 15px;
                }
            }
        }
        .marketPlace-detailCat__footer{
            padding: 18px 0;
            text-align: center;
            .see__more{
                padding: 0;
                font-family: Telefonica;
                font-style: normal;
                font-weight: 400;
                font-size: 18px;
                line-height: 24px;
                text-align: right;
                color: #019bf4;
                background: #fff;
                box-shadow: inherit;
                .text-dinamic:before {
                    content: 'Cargar más';
                }
                .icon {
                    font-size: 10px;
                    margin-left: 8px;
                }
                &.active{                    
                    .icon {
                        display: inline-block;
                        transform: rotate(180deg) translate(0,3px);
                    }
                    .text-dinamic:before {
                        content: 'Ocultar';
                    }
                } 
            }
        }
        .item--collapse{
            display: none;
        }
    }
    .marketPlace-result{
        .result__header{
            padding: 10px 0 13px;
            @media only screen and (min-width: 992px){
                padding: 8px 0;
            }
            .title{
                font-family: Telefonica;
                font-style: normal;
                font-weight: normal;
                color: #50535A;
                line-height:0;
                margin: 0;
                small{
                    font-size: 18px;
                    line-height: 22px;
                    display:block;
                }
                span{
                    font-size: 32px;
                    line-height: 40px;
                }
            }
        }
        .result__content{
            padding: 20px 0;
            @media only screen and (min-width: 992px){
                padding: 40px 0;
            }
            .list{
                margin: -10px;
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                @media only screen and (min-width: 992px){
                    margin: -15px;
                }
            }   
            .item{
                padding: 10px;
                margin: 0;
                @media only screen and (min-width: 992px){
                    padding: 15px;
                }
            }
        }
        .result__not-found{
            padding: 37px 0 40px;
            @media only screen and (min-width: 992px){
                padding: 40px 0;
            }
            .picture{
                text-align: center;
                margin-bottom: 17px;
                @media only screen and (min-width: 992px){
                    margin-bottom: 40px;
                }
                img{
                    width: 140px;
                    @media only screen and (min-width: 992px){
                        width: auto;
                    }
                }
            }         
            .title{
                font-family: Telefonica;
                font-style: normal;
                font-weight: 300;
                font-size: 24px;
                line-height: 30px;
                text-align: center;
                color: #019DF4;
                margin: 0 0 10px;
                @media only screen and (min-width: 992px){
                    font-size: 32px;
                    line-height: 40px;
                    margin: 0;
                }
            }
            .text{
                font-family: Telefonica;
                font-style: normal;
                font-weight: 300;
                font-size: 16px;
                line-height: 20px;
                text-align: center;
                color: #50535A;
                margin: 0;
                @media only screen and (min-width: 992px){
                    font-size: 18px;
                    line-height: 22px;
                }
            }
        }
    }    
    svg{
        display: inline-block;
        font-size: inherit;
        width: 1em;
        height: 1em;
        overflow: visible;
        stroke-width: 0;
        stroke: currentColor;
        fill: currentColor;
    }
}

.nateevo-marketPlace-solution{
    background: #0B2739;
    padding: 20px 0;
    @media only screen and (min-width: 992px){
        padding: 40px 0;
    }
    .container{
        width:100%;
        max-width: 1248px;
        padding: 0 15px;
    }
    .marketPlace-solution__header{
        .title{
            font-family: Telefonica;
            font-style: normal;
            font-weight: 300;
            font-size: 32px;
            line-height: 40px;
            text-align: center;
            color: #019DF4;
            padding: 20px 0;
            margin: 0;
            @media only screen and (min-width: 992px){
                font-size: 40px;
                line-height: 50px;
                padding: 25px 0;          
            }
        }
    }
    .marketPlace-solution__content{        
        .list{
            margin: 0 -10px;
            @media only screen and (min-width: 992px){
                margin: 0 -15px;
            }
        }
        .item{
            padding: 0 10px;
            @media only screen and (min-width: 992px){
                padding: 0 15px;
            }
        }
        .slick-list{
            padding: 30px 0 20px;
            @media only screen and (min-width: 992px){
                padding: 40px 0;
            }
        }
        .slick-dots{
            margin-top: 36px;
            position: relative;
            bottom: 0;
            line-height: 0;
            li{
                width: 8px;
                height: 8px;
                border-radius: 200px;
                button{
                    background: #FFFFFF;
                }
                &.slick-active button{
                    background: #019DF4;
                }
            }
        }
    }

}
.card--marketPlace{
    border: 1px solid #D3D4D3;
    border-radius: 11px;
    box-shadow: inherit;
    margin: 0;
    .bg--morado{
        background: #A13EA1;
    }
    .bg--cian{
        background: #019DF4;
    }
    .card-image{
        min-height: 69px;
        display: flex;
        align-items: center;
        background: #F5F5F5;
        border-radius: 11px 11px 0 0;
        @media only screen and (min-width: 992px){
            min-height: 120px;
        }
        img{
            width: auto;
            height: 45px;
            min-width: 100px;
            max-width: 135px;
            margin: 0 auto;
            padding: 0 10px;
            @media only screen and (min-width: 992px){
                width: auto;
                height: auto;
                min-width: 72px;
                max-width: 238px;
                padding: 0 15px;                
            }
        }
    }
    .card-promotion{
        font-family: Telefonica;
        font-style: normal;
        font-weight: normal;
        font-size: 13px;
        line-height: 16px;
        text-align: center;        
        color: #FFFFFF;
        padding: 0 1%;
        margin: 0 -1px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        @media only screen and (min-width: 992px){
            font-size: 16px;
            line-height: 20px;
        }
    }
    .card-content{
        padding: 12px 4px 8px;
        @media only screen and (min-width: 992px){
            padding: 16px 15px 8px;
        }
        .title{
            margin: 0;
            font-family: Telefonica;
            font-style: normal;
            font-weight: normal;
            text-align: center;
            line-height: 0;
            b{
                font-size: 18px;
                line-height: 22px;
                color: #50535A;
                display: block;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                @media only screen and (min-width: 992px){
                    font-size: 24px;
                    line-height: 30px;                    
                }
            }
            small{
                font-size: 12px;
                line-height: 15px;
                color: #86888C;
                @media only screen and (min-width: 992px){
                    font-size: 14px;
                    line-height: 17px;                    
                }
            }
        }
        .text{
            font-family: Telefonica;
            font-style: normal;
            font-weight: 300;
            font-size: 12px;
            line-height: 16px;
            text-align: center;
            color: #50535A;
            margin: 11px 0 0;
            height: 88px;
            text-overflow: ellipsis;
            overflow: hidden;
            @media only screen and (min-width: 992px){
                font-size: 16px;
                line-height: 22px;     
                margin: 16px 0 0;           
            }
        }
    }
    .card-action{
        text-align: center;
        padding: 12px 0 14px;
        border-top: 1px solid #D3D4D3;
        .btn{
            height:auto;
            padding: 0;
            font-family: Telefonica;
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 24px;
            text-align: center;
            color: #A13EA1;
            background: transparent;
            box-shadow: inherit;
            .icon{
                font-size: 10px;
            }
        }
    }
    .card-action:last-child{
        border-radius: 0 0 11px 11px;
    }
    svg{
        display: inline-block;
        font-size: inherit;
        width: 1em;
        height: 1em;
        overflow: visible;
        stroke-width: 0;
        stroke: currentColor;
        fill: currentColor;
    }
    &.card--marketPlace-active{
        border: 3px solid #019DF4;
        border-radius: 0 11px 11px 11px;
        &:before{
            content: '¡Nuevo!';
            position: absolute;
            background: #019df4;
            font-family: Telefonica;
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 28px;
            text-align: center;
            color: #fff;
            width: 79px;
            height: 28px;
            top: -31px;
            left: -3px;
            border-radius: 11px 11px 0 0;
        }
    }
}